<template>
    <div class="loginPage">
        <el-card class="box-card">
            <div class="logo" style="text-align: center;color: rgb(64, 158, 255);">
                <i class="el-icon-s-shop" style="font-size: 60px;"></i>
                <h2>shop_manage</h2>
            </div>
            <el-form :model="loginForm" status-icon :rules="rules" ref="ruleForm" class="demo-ruleForm">
                <el-form-item prop="username">                 
                    <el-input v-model="loginForm.username" autocomplete="on" placeholder="请输入用户名" clearable>
                        <i class="el-icon-user-solid" slot="prefix" style="color: rgb(64, 158, 255);"></i>
                    </el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input type="password" v-model="loginForm.password" autocomplete="off" placeholder="请输入内容" show-password>
                        <i class="el-icon-lock" slot="prefix" style="color: rgb(64, 158, 255);"></i>
                    </el-input>
                </el-form-item>
                <el-form-item style="text-align: center;">
                    <el-button style="width: 45%;" @click="submitForm" type="primary">登录</el-button>
                    <el-button style="width: 45%;" >获取账号</el-button>               
                </el-form-item>
            </el-form>
        </el-card>
    </div>   
</template>

<script>
import {loginTest} from '../../api/data'
export default {
    name: "login",
    data() {
        return {
            loginForm: {
                username: '',
                password: ''
            },
            rules: {
                username: [
                    { required: true, message: '请输入账号', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 3, message: '密码长度要大于7', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        submitForm() {
            loginTest(JSON.stringify(this.loginForm)).then((res) => {
                if(res.data.code === 200) {
                    this.$store.commit('setToken')
                    this.$router.push({name: 'Home'})
                }else if(res.data.code === 400) {
                    this.$message({
                        message: '账号密码错误',
                        type: 'warning'
                    })
                }
            })
        },
        resetForm() {

        },
    },
}
</script>

<style lang="scss" scoped>
.loginPage{
    height: 100%;
    width: 100%;
    background: url('https://fuss10.elemecdn.com/9/bb/e27858e973f5d7d3904835f46abbdjpeg.jpeg') round;
    .el-card{
        width: 358px;
        height: 350px;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        .logo{
            h2{
                line-height: 1.15;
                padding: 19.2px;
            }
        }
    }
}
</style>